	module.exports = {"data":{"markdownRemark":{"html":"<h2>Planning Poker</h2>\n<p>Planning poker is an estimation technique. It is often part of Agile Coache’s toolboxes. </p>\n<p>Each estimator holds poker cards with various values (often Fibonacci Sequence numbered). The value represents the number of days, workforces, story points or any kind of estimation that the team wants to meet in common. Then Product Owner (PO) / stakeholder explains the feature / item to estimate. The team can ask questions and discuss with PO to get a better understanding on <em>what</em> she/he wants. </p>\n<p>After the end of the discussion, each person selects a card which shows their estimation. Then they reveal it at the same time. It is of high value to the team to have the team members with lowest and highest estimates discuss, why they have picked so different estimations. </p>\n<p>Because Planning Poker is a tool to moderate a <em>joint</em> expert estimation, it leads to a better common understanding and perhaps refinement of the feature request. So Planning Poker is even of high value when the team is operating in a No-Estimates mode. As moderator try to avoid confirmation bias. </p>\n<h3>More Information:</h3>\n<ul>\n<li>Planning poker video: <a href='https://www.youtube.com/watch?v=MrIZMuvjTws' target='_blank' rel='nofollow'>YouTube</a></li>\n</ul>","fields":{"slug":"/agile/planning-poker/"},"frontmatter":{"title":"Planning Poker"}}},"pathContext":{"slug":"/agile/planning-poker/"}}

